"use client"; // Ensure Framer Motion works in Next.js App Router

import { motion, useMotionValue, useTransform } from "framer-motion";
import { useState, useEffect, useRef } from "react";
import Image from "next/image";

interface MotionImageProps {
  src: string;
  alt: string;
  heading: string;
  subheading: string;
  className?: string;
  reverse?: boolean;
}

const MotionImage: React.FC<MotionImageProps> = ({ src, alt, heading, subheading, className, reverse }) => {
  const [isHovered, setIsHovered] = useState(false);
  const imageHoverRef = useRef<HTMLDivElement | null>(null);
  const imageRef = useRef<HTMLDivElement | null>(null);

  // Translating and scaling based on cursor position
  const x = useMotionValue(0);
  const y = useMotionValue(0);

  // Transformations for translation and zooming
  const translateX = useTransform(x, [-50, 50], [-20, 20], { 
    type: "spring", 
    stiffness: 200, 
    damping: 25, 
    delay: 0.1,
  });

  const translateY = useTransform(y, [-50, 50], [-20, 20], {
    type: "spring", 
    stiffness: 200, 
    damping: 25, 
    delay: 0.1, 
  });

  const scale = useTransform([x, y], ([x, y]) => {
    const maxTranslation = Math.max(Math.abs(x), Math.abs(y));
    const zoomFactor = 1 + maxTranslation / 150;
    return zoomFactor > 1.05 ? 1.05 : zoomFactor; // Capped at 1.05 zoom
  }, {
    type: "spring", 
    stiffness: 200, 
    damping: 25, 
    delay: 0.1, 
  });

  // Mouse movement tracking function for cursor-based translation
  useEffect(() => {
    const handleMouseMove = (e: MouseEvent) => {
      const { offsetWidth: width, offsetHeight: height } = imageHoverRef.current!;
      let { offsetX: x, offsetY: y } = e;

      const xWalk = Math.round((x / width * 100) - 50) / 4;
      const yWalk = Math.round((y / height * 100) - 50) / 6;

      x.set(xWalk);
      y.set(yWalk);
    };

    const imageHoverContainer = imageHoverRef.current!;
    const imageHover = imageRef.current!;

    imageHover.style.transition = 'transform 0.3s ease-out'; // Smooth transition for scaling/translation

    const resetTransform = () => {
      imageHover.style.transform = `scale(1.0) translate(0px, 0px)`;
    };

    // Add event listeners for mousemove and mouseleave
    imageHoverContainer.addEventListener('mousemove', handleMouseMove);
    imageHoverContainer.addEventListener('mouseleave', resetTransform);

    return () => {
      imageHoverContainer.removeEventListener('mousemove', handleMouseMove);
      imageHoverContainer.removeEventListener('mouseleave', resetTransform);
    };
  }, [x, y]);

  return (
    <div 
      id={heading + "-hover-container"} 
      className={`w-full ${!reverse && `flex flex-col`} sm:flex-col-reverse group`} 
      onMouseEnter={() => setIsHovered(true)} 
      onMouseLeave={() => setIsHovered(false)} 
    >
      <header className="flex text-base sm:text-sm sm:block my-2 lg:text-base lg:leading-5">
        <h1 className="hidden lg:block font-black sm:italic">
          {heading} â€” <span className="font-medium sm:normal-case">{subheading}</span>
        </h1>
      </header>

      <div className="w-full overflow-hidden">
        <motion.div
          id={heading + "-hover"}
          ref={imageRef}
          className="w-full h-full"
          style={{
            translateX, 
            translateY, 
            scale
          }}
        >
          {src && (
            <Image
              src={src}
              alt={alt}
              layout="responsive" // Ensures the image scales responsively
              width={600} // Adjusted aspect ratio width
              height={800}
              className="w-full h-full object-cover rounded-lg" // Ensure it fully covers container without skewing
              priority
            />
          )}
        </motion.div>
      </div>
    </div>
  );
};

export default MotionImage;
